$welcome-dark: #26276a;
$welcome-light: #2c367e;
$welcome-background: #a1a6cc;
$welcome-touch: #e7e9f7;
.unioncloud-sub-site-option #union-logo a {
  background-image: url("//nusdigital.s3.amazonaws.com/union_logo/images/41/original/Union_400_px.jpg");
}

// TODO Temporary Welcome Week Fix
body#uc-page-id-settle-in-with-umii,
body#uc-page-id-who-we-are,
body[id^="union-event-id-"] {
  .title h1 {
    color: $white;
    background: $welcome-dark;
  }
  .wrapper .row.content {
    background-color: $welcome-touch;
    border-bottom: solid 10px $welcome-light;
    #uc-events-details-page {
      h1,
      h2,
      h3,
      h4 {
        color: $welcome-dark;
      }
      .add-to-calendar .title {
        background-color: $welcome-dark;
      }
    }
  }
}

#hero-image {
  margin-bottom: 0;
  .hero {
    padding: 3em;
    background-color: $welcome-dark;
    background-image: linear-gradient(#{$welcome-light} 55%, #{$welcome-dark});
    img {
      display: block;
      width: 400px;
      margin: 0 auto;
    }
  }
}

#blocks {
  .body-width-wrap {
    box-sizing: border-box;
    padding: 0 1%;
    width: 100%;
    max-width: 1170px;
    margin: 0 auto;
  }
  #section-head {
    margin-top: 0;
    background-color: $welcome-light;
  }
  .block-menu {
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1170px;
    li {
      box-sizing: border-box;
      min-width: 46%;
      background-color: $welcome-dark;
      height: auto;
      padding-bottom: 0.6em;
      img {
        border-color: $welcome-light;
        width: 100% !important;
        height: auto !important;
      }
      a {
        color: $white;
      }
    }
  }
}

#calendar-wrap {
  max-width: 1440px;
  margin: 0 auto;
}

#welcome-calendar-frame {
  height: 1200px;
}

.welcome-calendar-page {
  background: $welcome-background;
}

#calendar.welcome-calendar {
  caption {
    display: none;
  }
  tbody tr,
  tbody tr td {
    border-color: $welcome-dark;
    background-color: $white;
  }
  tbody .days:nth-of-type(5) td.day:hover,
  td.day:hover {
    background-color: $welcome-background;
  }
  th {
    background: $welcome-dark;
  }
  tbody .days:nth-of-type(2),
  tbody .days:nth-of-type(3) {
    display: none;
  }
  tbody .days:nth-of-type(5) td {
    background-color: $welcome-touch;
    .event {
      background-color: $white;
    }
  }
  .date {
    background-color: $welcome-background;
    color: $white;
  }
  td.day {
    box-sizing: border-box;
    width: 100%;
  }
  .event {
    background: $welcome-touch;
    font-size: 1em;
    padding: 0.5em;
    // margin: 0.5em 0.5em;
    border: $welcome-light;
    .event-desc {
      margin: 0;
      a {
        // font-size: 1.2em;
        color: $welcome-light;
        font-weight: bold;
      }
    }
    .event-time {
      font-size: 0.75em;
    }
