}

.unioncloud-sub-site-option .row.content {
  background-color: $welcome-background;
}

@media (max-width: 769px) {
  #calendar.welcome-calendar {
    .date {
      border-radius: unset;
      background-color: $welcome-dark;
      color: $white;
      padding: 0.5em 1em;
      width: calc(100% - 2em);
    }
  }
}
